@import '../../../app/styles/_variables';
@import '../../../app/styles/_typography';

.calling__page__wrapper {

    font-size: xx-small !important;
    color: rgb(123, 120, 120) !important;
}

tfoot,
th,
thead,
tr {
    font-size: small !important;
    color: rgb(143, 156, 169) !important;
}

tbody,
td {
    font-size: small !important;
    color: rgb(52, 57, 67) !important;
}



.badge,
.badge__success,
.badge__warning,
.badge__successActive,
.badge__warningActive {
    padding: 5px;
    border-radius: 9px;

    width: 120px !important;
    height: 22px !important;

    font-family: $font-family-base !important;
    font-style: normal !important;
    font-weight: 200 !important;
    font-size: 12px !important;
    line-height: 12px !important;
    letter-spacing: -0.2px !important;
    color: white;
}

.badge__success,
.badge__result {
    background-color: white !important;
    border: 1px solid $accountant !important;
    color: grey !important;


}

.badge__successActive {
    background-color: $accountant !important;


}

.badge__warning {
    background-color: white !important;
    border: 1px solid $accountant-big !important;
    color: grey !important;

}

.badge__warningActive {
    background-color: $accountant-big;
}



.badge__star,
.badge__error,
.badge__errActive,
.badge__blue,
.badge__star,
.badge__starActive,
.badge__cancel,
.badge__tolong {
    cursor: pointer;
    min-width: 60px !important;
    height: 19px !important;
    border-radius: 17px !important;
    font-size: 12px !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    flex-direction: row;

}

.actionsCell {
    height: 30px !important;
    display: flex !important;
    justify-content: flex-start !important;
    align-items: center !important;
    flex-direction: row;
}

.badge__error {


    color: $btn-color-error !important;
    border: 1px solid $btn-color-error !important;
    // background-color: $btn-color-error;
}

.badge__error:hover {

    background-color: $btn-color-error !important;
    color: white !important;
}

.badge__errActive,
.badge__errActive:hover,
.badge__tolong:hover {
    background-color: $accountant-big !important;
    color: white !important;

}
.badge__tolong{
    color: $accountant-big !important;
    border: 1px solid $accountant-big  !important;
}
.badge__blue {
    background-color: $btx-blue;
}

.badge__star {

    background-color: rgba(255, 255, 255) !important;
    color: rgb(96, 113, 124) !important;
    border: 1px solid $btx-blue !important;
}

.badge__starActive,
.badge__star:hover {

    background-color: $btx-blue !important;
    color: white !important;
}


.badge__cancel {
    background-color: rgba(71, 71, 73, 0.929) !important;
    color: rgb(255, 255, 255) !important;
    // border: 1px solid rgba(26, 26, 28, 0.901) !important;
}

.statusSuccess,
.statusExpired,
.statusWarning {
    font-weight: 600 !important;
    font-size: 11px !important;


}

.statusSuccess {
    // color: rgba(213, 138, 245, 0.901) !important;
    color: $accountant !important;
    // color: $accountant-big;
}

.statusExpired {

    color: $accountant-big !important;
}


.statusWarning {
    // color: rgba(213, 138, 245, 0.901) !important;
    color: $btn-color-error !important;
    // color: $accountant-big;
}